<template>
  <div>
    <!--  width    -->
    <!--   -->
    <h-nav current-page="mypage"></h-nav>-
    <b-container class="mt-3">
      <b-row>
        <h2>My Page</h2>
      </b-row>
      <b-row class="mt-4">
        <b-tabs vertical class="mytabs w-100" pills>
          <b-tab title="">
            <b-container>
              <b-row class="mt-3">
                <b-col md="2" class="colboard"></b-col>
                <b-col>{{user_id}}</b-col>
              </b-row>
              <b-row class="mt-3">
                <b-col md="2" class="colboard"></b-col>
                <b-col>{{user_nickname}}</b-col>
              </b-row>
              <b-row class="mt-3">
                <b-col md="2" class="colboard"></b-col>
                <b-col>{{emailnow}}</b-col>
              </b-row>
            </b-container>
          </b-tab>
          <b-tab title=" ">
            <b-row class="mt-3">
              <b-col md="4" class="colboard"> </b-col>
              <b-col cols="7">{{user_nickname}}</b-col>
            </b-row>
            <b-row class="mt-3">
              <b-col md="4" class="colboard"> </b-col>
              <b-col cols="7">
                <b-form-input v-model="nickwill" placeholder="  "></b-form-input>
              </b-col>
            </b-row>
            <b-row class="mt-3">
              <b-col md="4" class="colboard"> </b-col>
              <b-col cols="7">{{this.emailnow}}</b-col>
            </b-row>
            <b-row class="mt-3">
              <b-col md="4" class="colboard"> </b-col>
              <b-col cols="7">
                <b-form-input v-model="emailwill" placeholder="  "></b-form-input>
              </b-col>
            </b-row>
            <b-row class="mt-3">
              <b-col md="4" class="colboard"> </b-col>
              <b-col cols="7">
                <b-form-input
                  v-model="pwnow"
                  placeholder=" ."
                  :disabled="$wait.is('changeloading')"
                ></b-form-input>
              </b-col>
            </b-row>
            <!--   -->
            <b-button variant="danger" @click="changeInfo()" :disabled="$wait.is('changeloading')">
              <v-wait for="changeloading">
                <template slot="waiting">
                  <div class="d-flex justify-content-center">
                    <b-spinner label="Loading..." small></b-spinner>
                  </div>
                </template>
                
              </v-wait>
            </b-button>
          </b-tab>
          <b-tab title=" ">
            <b-row class="mt-3">
              <b-col md="4" class="colboard"></b-col>
              <b-col cols="7">
                <b-form-input v-model="pwnow" placeholder=" "></b-form-input>
              </b-col>
            </b-row>
            <b-row class="mt-3">
              <b-col md="4" class="colboard"> </b-col>
              <b-col cols="7">
                <b-form-input v-model="pwwill1" placeholder="  "></b-form-input>
              </b-col>
            </b-row>
            <b-row class="mt-3">
              <b-col md="4" class="colboard">
                <div> </div>
                <div></div>
              </b-col>
              <b-col cols="7">
                <b-form-input v-model="pwwill2" placeholder="   "></b-form-input>
              </b-col>
            </b-row>
            <b-row>
              <b-col></b-col>
              <b-col>
                <button class="change mt-2 ml-5"> </button>
              </b-col>
            </b-row>
          </b-tab>
          <b-tab title="   ">
            <div class="col-6">
            <h3>Draggable {{ draggingInfo }}</h3>
            <draggable
              :list="list"
              :disabled="!enabled"
              class="list-group"
              ghost-class="ghost"
              :move="checkMove"
              @start="dragging = true"
              @end="dragging = false"
            >
              <div
                class="list-group-item"
                v-for="element in list"
                :key="element.name"
              >{{ element.name }}</div>
            </draggable>
            </div>
          </b-tab>
          <b-tab title="">
            <b-row>
              <b-col>
                <p>     .</p>
                <p>    .</p>

                <div>
                  <b-input-group>
                    <b-form-input
                      v-model="withdraw.password"
                      placeholder=" ."
                      :disabled="$wait.is('withdrawloading')"
                    ></b-form-input>
                    <b-input-group-append>
                      <b-button
                        variant="danger"
                        @click="withdrawUser()"
                        :disabled="$wait.is('withdrawloading')"
                      >
                        <v-wait for="withdrawloading">
                          <template slot="waiting">
                            <div class="d-flex justify-content-center">
                              <b-spinner label="Loading..." small></b-spinner>
                            </div>
                          </template>
                          
                        </v-wait>
                      </b-button>
                    </b-input-group-append>
                  </b-input-group>
                </div>
              </b-col>
            </b-row>
          </b-tab>
        </b-tabs>
      </b-row>
    </b-container>-
    <loading v-wait:visible="'changeloading'"></loading>
    <loading v-wait:visible="'withdrawloading'"></loading>
    <b-modal id="infomodal" :title="infobox.title" ok>{{ infobox.content }}</b-modal>
  </div>
</template>
<script>
import NavbarVue from "../components/Navbar.vue";
import * as session from "../utils/loginService";
import Loading from "../components/Loading.vue";
import draggable from "vuedraggable";
export default {
  data: function() {
    return {
      //***************************************** */
      enabled: true,
      list: [
        { name: "John", id: 0 },
        { name: "Joao", id: 1 },
        { name: "Jean", id: 2 }
      ],
      dragging: false,
      //**************************************** */
      withdraw: {
        password: ""
      },
      infobox: {
        title: "",
        content: ""
      },

      user_id: "",
      user_nickname: "",
      emailnow: "",
      pwnow: "",
      pwwill1: "",
      pwwill2: "",
      nickwill: "",
      emailwill: "",
      currentTab: 0,
      tabs: [
        "",
        " ",
        " ",
        "   ",
        ""
      ]
    };
  },
  computed: {
    draggingInfo() {
      return this.dragging ? "under drag" : "";
    }
  },
  components: {
    "h-nav": NavbarVue,
    loading: Loading,
    draggable
  },
  mounted: function() {
    console.log(session.getRefreshToken());
    if (session.getRefreshToken() == null) {
      alert(" !");
      location.href = "/login";
    } else {
      this.user_id = session.getUsername();
      this.user_nickname = session.getNickname();
      session.get(session.apiurl + "login").then(response => {
        this.emailnow = response.data.email;
      });
    }
  },
  methods: {
    checkMove: function(e) {
      window.console.log("Future index: " + e.draggedContext.futureIndex);
    },
    withdrawUser() {
      this.$wait.start("withdrawloading");
      session
        .del(session.apiurl + "login", { password: this.withdraw.password })
        .then(response => {
          alert(" .");
          this.$wait.end("withdrawloading");
        })
        .catch(error => {
          if (error.response) {
            this.infobox.title = "  ";
            this.infobox.content = "  .";
            this.$bvModal.show("infomodal");
          }
          this.$wait.end("withdrawloading");
        });
    },
    changeInfo() {
      this.$wait.start("changeloading");
      this.infobox.title = "";
      this.infobox.content = "";
      this.$bvModal.show("infomodal");
      this.$wait.end("changeloading");
    }
  }
};
</script>
<style scoped>
.nav-link.active {
  background-color: black;
}

