<template>
    <div>
        <div class="example text-center mt-1" style="box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);padding: 10px;margin-bottom: 30px;">
            
            <b-form-select v-model="selected" :options="main_options" class="form-control mt-2 mb-2">
            </b-form-select>&nbsp;
            <b-form-select v-model="sub_selected" :options="sub_options" class="form-control mt-2 mb-2">
            </b-form-select>&nbsp;
            <b-button squared type="submit" class="btn mt-2 mb-2" @click="b()"></b-button>
        </div>
        <br />
        <div class='map_wrap' style="box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);padding: 10px;margin-bottom: 30px;">
          <div id="map" style="width:100%;height:100%;position:relative;overflow:hidden;"></div>
            <div id="menu_wrap" class="bg_white">
              <div class="option">
              </div>
              <hr>
              <ul id="placesList"></ul>
              <div id="pagination"></div>
          </div>
        </div>
    </div>
</template>
<script>
import Vue from 'vue';
import NavbarVue from "../components/Navbar.vue";

export default {
    data() {
        return {
            
            selected: null,
            main_options: [
                { value: null, text: '' },
                // { value: { cd: 6110000, name: '' }, text: '' },
                // { value: "", text: '(disabled)', disabled: true }
            ],
            sub_selected: null,
            sub_options: [
                { value: null, text: ',,' },
                { value: { cd: 3220000, name: '' }, text: '' },
                { value: { cd: 3240000, name: '' }, text: '' },
                { value: { cd: 3080000, name: '' }, text: '' },
                { value: { cd: 3150000, name: '' }, text: '' },
                { value: { cd: 3200000, name: '' }, text: '' },
                { value: { cd: 3040000, name: '' }, text: '' },
                { value: { cd: 3160000, name: '' }, text: '' },
                { value: { cd: 3170000, name: '' }, text: '' },
                { value: { cd: 3100000, name: '' }, text: '' },
                { value: { cd: 3090000, name: '' }, text: '' },
                { value: { cd: 3050000, name: '' }, text: '' },
                // {value:{cd:,name:''},text:''},
            ],
        }
    },
    methods: {
        b() {
          infowindow = new kakao.maps.InfoWindow({ zIndex: 1 });
          var temp = this.sub_selected.name + " ";
          ps.keywordSearch(temp, placesSearchCB);
        }
    },
    mounted() {
        console.log("welcome");
        container = document.getElementById("map"); //   DOM 
        options = {
            //     
            center: new kakao.maps.LatLng(33.450701, 126.570667), // .
            level: 3 // (,  )
        };
        map = new kakao.maps.Map(container, options);
        ps = new kakao.maps.services.Places();
    },
    
};
let infowindow = null; //      
let markers = [];
let container='';
let options='';
let ps='';
let map='';
function placesSearchCB(data, status, pagination) {
    if (status === kakao.maps.services.Status.OK) {
        var listEl = document.getElementById('placesList'), 
        menuEl = document.getElementById('menu_wrap'),
        fragment = document.createDocumentFragment(), 
        bounds = new kakao.maps.LatLngBounds(), 
        listStr = '';
        //       
        // LatLngBounds   
        var bounds = new kakao.maps.LatLngBounds();
        removeAllChildNods(listEl);
        removeMarker();
        for (var i = 0; i < data.length; i++) {
            var placePosition = new kakao.maps.LatLng(data[i].y, data[i].x),
                marker = addMarker(placePosition, i),
                itemEl = getListItem(i, data[i]); //    Element 
            // displayMarker(data[i]);
            bounds.extend(placePosition);
            // console.log(data[i]);
            (function(marker, title) {
                kakao.maps.event.addListener(marker, "mouseover", function() {
                    displayInfowindow(marker, title);
                });

                kakao.maps.event.addListener(marker, "mouseout", function() {
                    infowindow.close();
                });
                itemEl.onmouseover =  function () {
                displayInfowindow(marker, title);
                };

                itemEl.onmouseout =  function () {
                    infowindow.close();
                };
            })(marker, data[i].place_name);
            fragment.appendChild(itemEl);
        }
        //     Elemnet 
        listEl.appendChild(fragment);
        menuEl.scrollTop = 0;
        //       
        map.setBounds(bounds);
    }else if (status === kakao.maps.services.Status.ZERO_RESULT) {

        alert('   .');
        return;

    } else if (status === kakao.maps.services.Status.ERROR) {

        alert('    .');
        return;
    }

}
function displayPagination(pagination) {
    var paginationEl = document.getElementById('pagination'),
        fragment = document.createDocumentFragment(),
        i; 

    //    
    while (paginationEl.hasChildNodes()) {
        paginationEl.removeChild (paginationEl.lastChild);
    }

    for (i=1; i<=pagination.last; i++) {
        var el = document.createElement('a');
        el.href = "#";
        el.innerHTML = i;

        if (i===pagination.current) {
            el.className = 'on';
        } else {
            el.onclick = (function(i) {
                return function() {
                    pagination.gotoPage(i);
                }
            })(i);
        }

        fragment.appendChild(el);
    }
    paginationEl.appendChild(fragment);
}
function getListItem(index, places) {

    var el = document.createElement('li'),
    itemStr = '<span class="markerbg marker_' + (index+1) + '"></span>' +
                '<div class="info">' +
                '   <h6><a href="'+places.place_url+'">' + places.place_name + '</a></h6>';

    if (places.road_address_name) {
        itemStr += '    <span>' + places.road_address_name + '</span>' +
                    '   <span class="jibun gray">' +  places.address_name  + '</span>';
    } else {
        itemStr += '    <span>' +  places.address_name  + '</span>'; 
    }
                 
      itemStr += '  <span class="tel">' + places.phone  + '</span>' +
                '</div>';           

    el.innerHTML = itemStr;
    el.className = 'item';

    return el;
}
function removeAllChildNods(el) {   
    while (el.hasChildNodes()) {
        el.removeChild (el.lastChild);
    }
}
function addMarker(position, idx, title) {
    var imageSrc =
        "http://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png", //   url,   
        imageSize = new kakao.maps.Size(36, 37), //   
        imgOptions = {
            spriteSize: new kakao.maps.Size(36, 691), //   
            spriteOrigin: new kakao.maps.Point(0, idx * 46 + 10), //       
            offset: new kakao.maps.Point(13, 37) //      
        },
        markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imgOptions),
        marker = new kakao.maps.Marker({
            position: position, //  
            image: markerImage
        });

    marker.setMap(map); //    
    markers.push(marker); //    

    return marker;
}

function removeMarker() {
    for (var i = 0; i < markers.length; i++) {
        markers[i].setMap(null);
    }
    markers = [];
}

function displayInfowindow(marker, title) {
    var content = '<div style="padding:5px;z-index:1;">' + title + "</div>";

    infowindow.setContent(content);
    infowindow.open(map, marker);
}

//    
function displayMarker(place) {
    //    
    var marker = new kakao.maps.Marker({
        map: map,
        position: new kakao.maps.LatLng(place.y, place.x)
    });
    // // console.log(Object.keys(marker).length);
    // for(var key in marker){
    //   markers.push(marker[key]);
    //   // console.log(typeof(key.setMap));
    // }

    //   
    kakao.maps.event.addListener(marker, "click", function() {
        //     
        infowindow.setContent(
            '<div style="padding:5px;font-size:12px;">' + place.place_name + "</div>"
        );
        infowindow.open(map, marker);
    });
}
</script>
<style scoped>
