    display: block;
    color: navy;
    text-align: center;
    text-decoration: none;
}

.menu_set a:hover {
    background-color: #bbdefb;
    color: navy;
    font-weight: bold;
}

.active {
    background-color: #bbdefb;
    color: navy;
    font-weight: bold;
    padding: 4px 8px;
}

.non_active {
    display: block;
    color: navy;
    text-align: center;
    text-decoration: none;
}

.form-control {
    width: 200px;
}

.example {
    display: table;
    border: 2px solid #5f90df ;
   
    margin: 0 auto;
}
.btn{
  background-color: #5f90df;
  color : white;
  border: 2px solid #EDF2F9 ;

}
.map_wrap, .map_wrap * {margin:auto;padding:0;font-family:'Malgun Gothic',dotum,'',sans-serif;font-size:12px;}
.map_wrap a, .map_wrap a:hover, .map_wrap a:active{color:#000;text-decoration: none;}
