<template>
  <div>
    <h-nav current-page="serviceinfo"></h-nav>
    <div>
       <b-nav class="menubar_set mt-5 justify-content-center ">
      
        <b-nav-item class="menu_set mb-2 " :to="{name:'oldplace',params:{no:1}}" :class="[isSelected(1) ? 'active':'non_active']" ></b-nav-item>
        <b-nav-item class="menu_set mb-2 " :to="{name:'oldwelfare',params:{no:2}}" :class="[isSelected(2) ? 'active':'non_active']" ></b-nav-item>
      </b-nav>
    </div>
    <b-container>
      <keep-alive>
      <router-view></router-view>
      </keep-alive>
    </b-container>
  </div>
</template>

<script>
import NavbarVue from "../components/Navbar.vue";
import axios from "axios";
import x2j from "xml2js";
import x22j from "xml-js";

export default {
  components: {
    "h-nav": NavbarVue
  },
  data() {
    return {
      bid:1,
      selected: null,
      options:[
        {value:null,text:','},
        {value:{cd:6110000,name:''},text:''},
        {value:"",text:'(disabled)', disabled:true}
      ],
      sub_selected: null,
      sub_options:[
        {value:null,text:',,'},
        {value:{cd:3220000,name:''},text:''},
        {value:{cd:3240000,name:''},text:''},
        {value:{cd:3080000,name:''},text:''},
        {value:{cd:3150000,name:''},text:''},
        {value:{cd:3200000,name:''},text:''},
        {value:{cd:3040000,name:''},text:''},
        {value:{cd:3160000,name:''},text:''},
        {value:{cd:3170000,name:''},text:''},
        {value:{cd:3100000,name:''},text:''},
        {value:{cd:3090000,name:''},text:''},
        {value:{cd:3050000,name:''},text:''},
        // {value:{cd:,name:''},text:''},
      ],
      gulist: []
    };
  },
  methods: {
    a() {
      var moveLatLon = new kakao.maps.LatLng(33.452613, 126.570888);
      map.setCenter(moveLatLon);
    },
    b() {
      infowindow = new kakao.maps.InfoWindow({ zIndex: 1 });
      var temp=this.sub_selected.name+" ";
      ps.keywordSearch(temp, placesSearchCB);
      
    },
    isSelected(boardId) {
      // console.log("bid: "+boardId);
      console.log(this.$route.params.no);
      //   $route  params   .
      if (boardId == this.$route.params.no||(this.$route.params.no==undefined&&boardId+1==2)) {
        return true;
      }
      return false;
    }
  
  },
  mounted(){
    this.bid=1;
  }
  
};
let infowindow = null; //      
let markers=[];

function placesSearchCB (data, status, pagination) {
    if (status === kakao.maps.services.Status.OK) {
        //       
        // LatLngBounds   
        var bounds = new kakao.maps.LatLngBounds();
        removeMarker();
        for (var i=0; i<data.length; i++) {
            var placePosition = new kakao.maps.LatLng(data[i].y, data[i].x),
            marker=addMarker(placePosition,i);   
            bounds.extend(placePosition);
            (function(marker, title) {
              kakao.maps.event.addListener(marker, 'mouseover', function() {
                  displayInfowindow(marker, title);
              });

              kakao.maps.event.addListener(marker, 'mouseout', function() {
                  infowindow.close();
              });
            })(marker,data[i].place_name);
        }       

        //       
        
        map.setBounds(bounds);
    } 
}
function addMarker(position, idx, title) {
    var imageSrc = 'http://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png', //   url,   
        imageSize = new kakao.maps.Size(36, 37),  //   
        imgOptions =  {
            spriteSize : new kakao.maps.Size(36, 691), //   
            spriteOrigin : new kakao.maps.Point(0, (idx*46)+10), //       
            offset: new kakao.maps.Point(13, 37) //      
        },
        markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imgOptions),
            marker = new kakao.maps.Marker({
            position: position, //  
            image: markerImage 
        });

    marker.setMap(map); //    
    markers.push(marker);  //    

    return marker;
}
function removeMarker() {
    for ( var i = 0; i < markers.length; i++ ) {
        markers[i].setMap(null);
    }   
    markers = [];
}
function displayInfowindow(marker, title) {
    var content = '<div style="padding:5px;z-index:1;">' + title + '</div>';

    infowindow.setContent(content);
    infowindow.open(map, marker);
}


//    
function displayMarker(place) {
    
    //    
        var marker = new kakao.maps.Marker({
        map: map,
        position: new kakao.maps.LatLng(place.y, place.x) 
    });
    

    //   
    kakao.maps.event.addListener(marker, 'click', function() {
        //     
        infowindow.setContent('<div style="padding:5px;font-size:12px;">' + place.place_name + '</div>');
        infowindow.open(map, marker);
    });
}

</script>
<style scoped>
.header {
  margin-top: 0.1em;
}
.menubar_set{  
list-style-type:none; 

padding-right: 20px;
padding-left: 15px;
margin-left: auto;
margin-right: auto;
}
.menu_set{float: left;}
.menu_set a{
  display: block; color: #5153c2; 
  text-align:center;
 text-decoration:none;
 
 }
.menu_set :hover{ 
color:#5153c2; 
font-weight:bold;

border-bottom:2px solid #5153c2;
}
.active{ 
color:#5153c2; 
font-weight:bold;

border-bottom: 2px solid #5153c2;
}
.non_active{
  display: block; color: navy; text-align:center;
 text-decoration:none;
}
@media (min-width: 768px) {
  .menubar_set {
    width: 760px;
    
  }
  .menu_set a{
    font-size: 18px;
  }
  
}

@media (min-width: 992px) {
